___CSS_LOADER_EXPORT___.push([module.id, `.sxa-social-links .social-links-container {
  text-align: center;
}
.sxa-social-links .social-links-title {
  margin: 0 0 20px 0;
  font-size: 1.25rem;
  font-weight: 600;
  color: var(--text-primary);
}
.sxa-social-links .social-links-list {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 15px;
  margin: 0;
  padding: 0;
  list-style: none;
  flex-wrap: wrap;
}
.sxa-social-links .social-link {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  width: 48px;
  height: 48px;
  background: var(--bg-surface);
  border: 1px solid var(--border-color);
  border-radius: 50%;
  color: var(--text-primary);
  text-decoration: none;
  transition: all 0.15s ease-in-out;
}
.sxa-social-links .social-link:hover {
  transform: translateY(-3px);
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.15);
}
.sxa-social-links .social-link .social-link-text {
  display: none;
}
.sxa-social-links[data-variant=text-icons] .social-link {
  width: auto;
  padding: 10px 18px;
  border-radius: 0.375rem;
}
.sxa-social-links[data-variant=text-icons] .social-link .social-link-text {
  display: inline;
  font-size: 14px;
  font-weight: 600;
}
.sxa-social-links[data-variant=colored] .social-link {
  border: none;
  color: white;
}
.sxa-social-links[data-variant=colored] .social-link[data-platform=facebook] {
  background: #1877f2;
}
.sxa-social-links[data-variant=colored] .social-link[data-platform=twitter] {
  background: #1da1f2;
}
.sxa-social-links[data-variant=colored] .social-link[data-platform=linkedin] {
  background: #0a66c2;
}
.sxa-social-links[data-variant=colored] .social-link[data-platform=instagram] {
  background: linear-gradient(45deg, #f09433 0%, #e6683c 25%, #dc2743 50%, #cc2366 75%, #bc1888 100%);
}
.sxa-social-links[data-variant=colored] .social-link[data-platform=youtube] {
  background: #ff0000;
}
.sxa-social-links[data-variant=monochrome] .social-link {
  background: transparent;
  border-color: var(--text-primary);
  color: var(--text-primary);
}
.sxa-social-links[data-variant=monochrome] .social-link:hover {
  background: var(--text-primary);
  color: var(--bg-body);
